/***/ (function(module, exports) {

	module.exports = {"data":{"markdownRemark":{"html":"<p>2017 has been a year full of work for me. Fun and Fascinating work.</p>\n<p>Recently, I transitioned from my Frontend development role at Quorum Analytics to a Data Science internship at Knotch. As can be expected with any job change, a lot was different in my new role. DC to New York - School + Work to hackNY + Work - Frontend to Data Science</p>\n<p>At the close of my fellowship with Knotch, I thought I'd write a bit about the nuances of this last difference and how they affected me. </p>\n<p>*** <strong>Disclaimer</strong>  My opinions are almost certainly affected by differences in the development environments I was working in, so take them with a grain of salt. ***</p>\n<h2 id=\"engineering-vs-science\"><a href=\"#engineering-vs-science\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Engineering VS Science</h2>\n<p>It's easy to forget that Data Science is a <em>science</em>, and not a subset of Software Engineering. The differences between science and engineering inform a lot of how my work-flow differed across these fields. </p>\n<p>In engineering, the work-flow consists of design, develop, and deploy. It is a cycle often called the \"maker schedule\" because of the focus on, what is effectively, construction. It tends to suit those who motivate themselves primarily by seeing the utility they bring their stakeholders directly and clearly.</p>\n<p>Science, even Data Science, follows the scientific method: Propose, Test, Modify. While you may eventually deploy a feature, there is always a nagging that you continue improving upon the same problem for the rest of your life. The field focuses on discovery and suits people who gather satisfaction from the novelty of new knowledge, even if the outcome has little clear utility in the present.</p>\n<h2 id=\"strategies-to-build-a-house\"><a href=\"#strategies-to-build-a-house\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Strategies to build a house</h2>\n<p>Engineering, especially when building for the web, tends to move incredibly quickly. How quickly you progress through a problem is almost directly correlated to the work you put in. Once a road-map is put in place for a feature at the start, you will ideally be checking boxes from that road-map with each passing day. It's like building a brick house. Each day you are able to place more bricks on top of the ones you placed the day before. </p>\n<p>On the other hand you have Data Science where, especially for Machine Learning projects, your experiments can take hours or days and the brick by brick work-flow is impossible. Instead, the key to being productive is learning to work in parallel whenever possible. Otherwise, you'll end up like this:</p>\n<p><img src=\"https://github.com/Helw150/WilliamHeldV2/blob/master/gatsby-files/pages/images/training.png?raw=true\" alt=\"A comic describing Data Scientists slacking using Machine Learning training as an excuse\"></p>\n<p>While one experiment runs, you need to be researching new methods, preparing follow-up experiments, or sleeping (running experiments overnight is a great way to parallelize). This work-flow is more like building a mud hut. You have to wait for one layer to dry before you can work on the next, but you can and should work on another wall while you wait. Otherwise you'll just sit around watching mud dry. </p>\n<h2 id=\"choosing-a-career-based-on-fragility-of-ones-ego\"><a href=\"#choosing-a-career-based-on-fragility-of-ones-ego\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Choosing a career based on fragility of ones ego</h2>\n<p>To be happy in a job, I find that one generally has to feel, to some extent, successful. Now some may feel successful every time they get a paycheck, but usually that success has to do more with whether or not they <em>contributed</em> to a larger goal. </p>\n<p>In engineering, there is almost always light at the end of the tunnel in terms of success. A feature may run slower than expected or require more engineering time than planned, but as an engineer you will almost always create a feature which performs it's task. Failure does and should happen, but it's rarer and it's generally less final.</p>\n<p>Data Science, however, tends to set loft goals for itself. Which is exciting! However, this also means that you are sometimes apt to fail in ways far more final. Whether you simply don't have enough data or your problem is too general to be solved well, making something that isn't usable is very possible. This doesn't mean you've failed, only removed a possibility from the table.</p>\n<p>If your definition of self worth clearly depends on clear success, data science can be hard. I can't imagine what my response would have been had my project had failed, but there were times where I felt it was a real possibility and you should be prepared for it.</p>\n<h2 id=\"my-transition-to-data-science\"><a href=\"#my-transition-to-data-science\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>My Transition to Data Science</h2>\n<p>When I began working as a Data Scientist, I found that I missed the constant small victories and the speed of the \"maker schedule\". I found myself struggling to understand the value of what I was doing as I researched and eliminated solutions with my experiments. </p>\n<p>When it worked, the satisfaction I got from the beautiful performance of <a href=\"https://www.cs.cmu.edu/~diyiy/docs/naacl16.pdf\">Hierarchical Attention Networks</a> on my task for Knotch made up for all of it in a fell swoop. I realized that all the days of experimenting and tweaking were key to being confident in my solution and therefore my satisfaction, but initially I hadn't prepared myself for the change. Looking back, even if the project had failed in the summer, I would have built valuable knowledge on the possibilities in Knotch's problem space. </p>\n<p>I wish that I had been prepared to shift my mindset from the start. The sections above are my attempt to express what I wish I had known going in, and some pitfalls I feel lucky having avoided. If you're going into data science, I hope the above helps you understand some of the more abstract differences to expect. If you're an experienced data scientist or engineer, I'm sure you smile knowingly of all the struggles I am yet to face in either field.</p>\n<p>Thanks for reading this far, whatever your reasons. This blog serves as a sort of a personal journal, but I am happy if it provided you even the slightest bit of utility. If you have critiques, comments, or plain curiosities feel free to reach out to me(at)WilliamHeld.com.</p>","timeToRead":4,"excerpt":"2017 has been a year full of work for me. Fun and Fascinating work. Recently, I transitioned from my Frontend development role at Quorum…","frontmatter":{"title":"From Frontend to Data Science","cover":"/images/React2Tf.png","date":"06/23/2017","category":"tech","tags":["frontend","data science","startup"],"description":"My experience working back to back as a Frontend engineer and a Data Scientist"},"fields":{"slug":"/from-frontend-to-data-science"}}},"pathContext":{"slug":"/from-frontend-to-data-science"}}

/***/ })

